.card{
    display: flex;
    flex-direction: column;
    background-color: rgb(88, 148, 103);
    border: 4px solid rgb(10, 92, 85);
    border-radius: 10px;
    box-shadow: 0 10px 30px;
    padding: 25px;
    max-width: 20vw;
    cursor: pointer;
    // -moz-osx-font-smoothing: grayscale;
    // backface-visibility: hidden;
    // transform: translateZ(0);
    transition: transform  0.25s ease-out; // smooths hover transition

    &:hover{
        transform: scale(1.05);
    }

    img.pic{
        max-width: 20vw;
        max-height: 20vw;
    }

    p.email{
        font-size: larger;
    }
}